
"use client";

import { useState, useEffect, useRef } from 'react';
import { Download, Upload, Settings, Loader2, Mic, Check } from 'lucide-react';
import { Button } from '@/components/ui/button';
import {
  Sheet,
  SheetContent,
  SheetDescription,
  SheetHeader,
  SheetTitle,
  SheetTrigger,
  SheetClose,
} from '@/components/ui/sheet';
import { useToast } from '@/hooks/use-toast';
import { exportTranscript } from '@/lib/export';
import type { TranscriptWord } from '@/lib/types';
import { transcribeAudio } from '@/ai/flows/speech-to-text-flow';
import { EditorView } from './editor-view';
import { cn } from '@/lib/utils';
import { Label } from './ui/label';
import { Input } from './ui/input';
import { Card } from './ui/card';

const blobToBase64 = (blob: Blob): Promise<string> => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onloadend = () => {
      if (typeof reader.result === 'string') {
        resolve(reader.result);
      } else {
        reject(new Error('Failed to convert blob to Base64.'));
      }
    };
    reader.onerror = reject;
    reader.readAsDataURL(blob);
  });
};


export function VoiceScribePage() {
  const [audioFile, setAudioFile] = useState<File | null>(null);
  const [isTranscribing, setIsTranscribing] = useState(false);
  const [structuredTranscript, setStructuredTranscript] = useState<TranscriptWord[]>([]);
  const [editedTranscript, setEditedTranscript] = useState('');
  const [wordsPerSecond, setWordsPerSecond] = useState<number | undefined>(undefined);

  const [exportFormat, setExportFormat] = useState('srt');
  const [isDragging, setIsDragging] = useState(false);
  const [isSettingsOpen, setIsSettingsOpen] = useState(false);

  const { toast } = useToast();
  const fileInputRef = useRef<HTMLInputElement | null>(null);

  useEffect(() => {
    const processAudio = async () => {
        if (!audioFile) return;

        setIsTranscribing(true);
        setStructuredTranscript([]);
        setEditedTranscript('');

        try {
            const audioDataUri = await blobToBase64(audioFile);
            const result = await transcribeAudio({ audioDataUri });
            
            if (result && result.transcript) {
                setStructuredTranscript(result.transcript);
                setEditedTranscript(result.text);
            } else {
                toast({
                    title: "Transcription failed",
                    description: "The model did not return a transcript. Please try again.",
                    variant: "destructive",
                });
            }
        } catch (e: any) {
            console.error(e);
            toast({
                title: "Transcription Error",
                description: e.message || "An error occurred while processing your audio.",
                variant: "destructive",
            });
        } finally {
            setIsTranscribing(false);
        }
    };
    processAudio();
  }, [audioFile, toast]);

  const handleFileSelect = (file: File | null | undefined) => {
    if (file && file.type.startsWith('audio/')) {
        setAudioFile(file);
    } else if (file) {
        toast({
            title: "Invalid file type",
            description: "Please select an audio file.",
            variant: "destructive",
        });
    }
  }

  const handleDragEvents = (e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.stopPropagation();
  };

  const handleDragEnter = (e: React.DragEvent<HTMLDivElement>) => {
    handleDragEvents(e);
    if (e.dataTransfer.items && e.dataTransfer.items.length > 0) {
        setIsDragging(true);
    }
  };

  const handleDragLeave = (e: React.DragEvent<HTMLDivElement>) => {
    handleDragEvents(e);
    setIsDragging(false);
  };
  
  const handleDrop = (e: React.DragEvent<HTMLDivElement>) => {
    handleDragEvents(e);
    setIsDragging(false);
    handleFileSelect(e.dataTransfer.files?.[0]);
  };

  const handleExport = () => {
    exportTranscript(editedTranscript, exportFormat as 'srt' | 'txt' | 'vtt', structuredTranscript, toast, wordsPerSecond);
    setIsSettingsOpen(false);
  };
  
  const formatDisplayNames: {[key: string]: string} = {
    srt: 'SRT (Subtitles)',
    vtt: 'VTT (Subtitles)',
    txt: 'TXT (Plain Text)',
  }

  const isReadyForContent = !isTranscribing && structuredTranscript.length > 0;

  return (
    <div 
        className="flex flex-col h-screen bg-background text-foreground"
        onDragEnter={handleDragEnter}
        onDragOver={handleDragEvents}
        onDragLeave={handleDragLeave}
        onDrop={handleDrop}
    >
        <header className="flex items-center justify-between p-4 border-b shadow-sm">
            <div className="flex items-center gap-2">
                <Mic className="h-6 w-6 text-primary" />
                <h1 className="text-xl font-bold tracking-tight">VoiceScribe</h1>
            </div>
        </header>

        <main className="flex-grow p-4 md:p-6 grid grid-cols-1 gap-6 relative bg-muted/40 overflow-y-auto">
            {isTranscribing ? (
                <div className="absolute inset-0 bg-background/80 flex flex-col items-center justify-center z-10">
                    <Loader2 className="h-12 w-12 animate-spin text-primary" />
                    <p className="text-muted-foreground mt-4 text-lg">Transcribing audio, please wait...</p>
                </div>
            ) : null}

            {!audioFile ? (
                <div 
                    className={cn(
                        "flex flex-col items-center justify-center text-center flex-grow rounded-lg border-2 border-dashed border-muted-foreground/20 bg-background h-full transition-colors",
                        isDragging && "border-primary bg-primary/10"
                    )}
                    onClick={() => fileInputRef.current?.click()}
                >
                    <Upload className="w-20 h-20 text-muted-foreground/30 mb-4"/>
                    <h3 className="text-2xl font-semibold">Ready to Transcribe</h3>
                    <p className="text-muted-foreground mt-2 mb-4">Drop an audio file here or click to upload.</p>
                </div>
            ) : (
                <Card className="flex flex-col h-full shadow-sm">
                    <EditorView
                        transcript={editedTranscript}
                        onTranscriptChange={setEditedTranscript}
                        disabled={!isReadyForContent}
                    />
                </Card>
            )}
        </main>
        
        <footer className="flex items-center justify-center gap-4 p-4 border-t bg-background shadow-sm">
             <input
                type="file"
                ref={fileInputRef}
                onChange={(e) => handleFileSelect(e.target.files?.[0])}
                className="hidden"
                accept="audio/mp3,audio/wav,audio/ogg,audio/m4a,audio/*"
            />
            
            <Sheet open={isSettingsOpen} onOpenChange={setIsSettingsOpen}>
                <SheetTrigger asChild>
                    <Button variant="default" size="lg" disabled={!isReadyForContent} className="rounded-full">
                        <Download className="h-5 w-5" />
                        <span className="ml-2 sm:inline">DOWNLOAD</span>
                    </Button>
                </SheetTrigger>
                <SheetContent side="bottom" className="rounded-t-lg">
                    <SheetHeader className="text-left">
                        <SheetTitle>Export Settings</SheetTitle>
                        <SheetDescription>
                            Choose your format and settings, then click export.
                        </SheetDescription>
                    </SheetHeader>
                    <div className="grid gap-6 py-6">
                        <div className="grid gap-3">
                            <Label>Export Format</Label>
                            <Sheet>
                                <SheetTrigger asChild>
                                    <Button variant="outline" className="justify-between">
                                        <span>{formatDisplayNames[exportFormat]}</span>
                                        <svg width="15" height="15" viewBox="0 0 15 15" fill="none" xmlns="http://www.w3.org/2000/svg" className="opacity-50"><path d="M4.93179 5.43179C4.75605 5.60753 4.75605 5.89247 4.93179 6.06821L7.5 8.63642L10.0682 6.06821C10.244 5.89247 10.244 5.60753 10.0682 5.43179C9.89247 5.25605 9.60753 5.25605 9.43179 5.43179L7.5 7.36358L5.56821 5.43179C5.39247 5.25605 5.10753 5.25605 4.93179 5.43179Z" fill="currentColor" fillRule="evenodd" clipRule="evenodd"></path></svg>
                                    </Button>
                                </SheetTrigger>
                                <SheetContent side="bottom" className="rounded-t-lg">
                                    <SheetHeader className="text-left py-4">
                                        <SheetTitle>Select Format</SheetTitle>
                                    </SheetHeader>
                                    <div className="pt-2">
                                        {Object.entries(formatDisplayNames).map(([value, label]) => (
                                            <SheetClose asChild key={value}>
                                                <Button 
                                                    variant="ghost" 
                                                    className="w-full justify-start text-base py-6"
                                                    onClick={() => setExportFormat(value)}
                                                >
                                                    <div className="flex items-center w-full">
                                                        <span>{label}</span>
                                                        {exportFormat === value && <Check className="ml-auto h-5 w-5 text-primary" />}
                                                    </div>
                                                </Button>
                                            </SheetClose>
                                        ))}
                                    </div>
                                </SheetContent>
                            </Sheet>
                        </div>
                        <div className="grid gap-3">
                            <Label htmlFor="words-per-second">Words per second</Label>
                            <Input 
                                id="words-per-second"
                                type="number"
                                placeholder="e.g. 1"
                                value={wordsPerSecond ?? ''}
                                onChange={(e) => setWordsPerSecond(e.target.value ? Number(e.target.value) : undefined)}
                                className="bg-background"
                            />
                            <p className="text-xs text-muted-foreground">For SRT/VTT. Overrides AI timing.</p>
                        </div>
                         <Button 
                            onClick={handleExport}
                            disabled={!isReadyForContent}
                            size="lg"
                        >
                            <Download className="mr-2" />
                            Export Transcript
                        </Button>
                    </div>
                </SheetContent>
            </Sheet>
        </footer>
    </div>
  );
}
